.App{
    display: flex;
    flex-direction: column;

&__back{
   
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: -1;
    
}

&__settings{
    padding: 15px 25px; 
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    height: 45vh;
    flex-wrap: wrap;
   
}
&__options{
  text-align: center;
    @media(min-width:475px){
        margin: 0;
        text-align: left;
              }
              @media(min-width:615px){
               
                text-align: center;
                      }
   
  &-data{
    background-color: transparent;
    margin: 0 25px; 
    width: auto;
    border: none;
    border-bottom: 1px solid yellow;
    outline: none;
    color: white;
    font-size: 0.9em; 
    margin-bottom: 20px;
    transition: all .3s;

    &--error{
        border-color: red;
    }
    @media(min-width:475px){
       margin-bottom: 0;
       margin: 15px 25px;
             }
               @media(min-width:600px){
            margin: 0 25px;
               }
}  

&-btn{
    border-radius: 3px;
    background-color: yellow;
    border: none;
   text-align: center;
text-transform: uppercase;

    font-size: 0.5em;
width: 140px;
padding: 10px 0;
letter-spacing: 2px;
font-weight: bold;
transition: all .3s;
outline: none !important;
@media(min-width:615px){
               
    margin-top: 20px;
          }
          @media(min-width:755px){
            margin-top: 0px;
          
  } 
&--error{
    background-color: red;
}
} 
}


}